<style scoped>
.expand-row {
  margin-bottom: 8px;
}
</style>
<template>
  <div>
    <Row>
      <Col span="6">
        <strong>
          <span class="expand-key">ClientID:</span>
        </strong>
      </Col>
      <Col span="18">
        <strong>
          <span class="expand-value" ref="clientId">{{ row.value.clientId}}</span>
        </strong>
      </Col>
    </Row>
    <Divider/>
    <Row>
      <Col span="6">
        <strong>
          <span class="expand-key">ChannelID:</span>
        </strong>
      </Col>
      <Col span="18">
        <strong>
          <span class="expand-value" ref="channelId">{{ row.value.channelId}}</span>
        </strong>
      </Col>
    </Row>
    <Divider/>

    <Row :gutter="16">
      <Col span="3">
        <div>
          <p>
            <strong>LastWillSet:</strong>
          </p>
        </div>
      </Col>
      <Col span="5">
        <div>
          <p>
            <Tag
              style="width:120px;"
              color="primary"
            >willTopic:{{ row.value.willMessageToJson.willTopic}}</Tag>
          </p>
        </div>
      </Col>
      <Col span="5">
        <div>
          <p>
            <Tag
              style="width:120px;"
              color="primary"
            >cleanSession:{{ row.value.willMessageToJson.will.cleanSession}}</Tag>
          </p>
        </div>
      </Col>
      <Col span="5">
        <div>
          <p>
            <Tag
              style="width:120px;"
              color="primary"
            >willFlag:{{ row.value.willMessageToJson.will.willFlag}}</Tag>
          </p>
        </div>
      </Col>
      <Col span="5">
        <div>
          <p>
            <Tag
              style="width:120px;"
              color="primary"
            >willRetain:{{ row.value.willMessageToJson.will.willRetain}}</Tag>
          </p>
        </div>
      </Col>
    </Row>
    <Divider/>
    <div style="margin-top:20px;">
      <h1>Topics</h1>
      <Table :columns="tableHeader" :data="topicList">
        <template slot-scope="{ row }" slot="clientId">
          <strong>{{ row.value.clientId}}</strong>
        </template>
        <template slot-scope="{ row }" slot="topicFilter">
          <strong>{{ row.value.topicFilter}}</strong>
        </template>

        <template slot-scope="{ row }" slot="mqttQos">
          <strong>{{ row.value.mqttQoS}}</strong>
        </template>
      </Table>
    </div>
  </div>
</template>
<script>
let config = require("../../../config/config.json");

export default {
  props: {
    row: Object,
    topics: []
  },
  data() {
    return {
      tableHeader: [
        {
          title: "ClientId",
          slot: "clientId"
        },
        {
          title: "Topic",
          slot: "topicFilter"
        },
        {
          title: "QOS",
          slot: "mqttQos"
        }
      ],
      topicList: []
    };
  },
  created() {},
  mounted() {
    this.getTopicInfo(this.$refs.clientId.innerText);
  },
  methods: {
    /**
     * http://localhost:2580/dashboard/searchTopicByChannelId?channelId=00a16
     */
    getTopicInfo(clientId) {
      let _this = this;
      this.axios
        .get(
          "http://" +
            config.host +
            ":" +
            config.port +
            config.context +
            "dashboard/searchTopicByClientId",
          {
            params: {
              clientId: clientId
            },
            headers: {
              web_console_token: localStorage.getItem("web_console_token")
            }
          }
        )
        .then(response => {
          //console.log("请求数据:", response.data.data);
          if (response.data.state == 1) {
            _this.topicList = response.data.data;
          } else {
            this.$Message.error("数据请求失败");
          }
        })
        .catch(response => {
          console.log(response);
        });
    }
  }
};
</script>