<template>
  <div>
    <Table
      type="expand"
      width="100%"
      height="680"
      border
      :columns="tableHeader"
      :data="pageData.data"
      @on-expand="onExpand"
      @on-row-click="onRowClick"
    >
      <template slot-scope="{ row }" slot="address">
        <strong>{{ row.value.channelToJson.address}}</strong>
      </template>
      <template slot-scope="{ row }" slot="active">
        <strong>{{ row.value.channelToJson.active}}</strong>
      </template>

      <template slot-scope="{ row }" slot="cleanSession">
        <strong>{{ row.value.cleanSession}}</strong>
      </template>
    </Table>
    <div style="text-align:right;margin-top:10px;">
      <Page
        transfer
        :page-size="10"
        prev-text="上一页"
        on-change="onPageChange"
        :total="pageData.total"
        show-total
        next-text="下一页"
      />
    </div>
  </div>
</template>
<script>
let config = require("../../config/config.json");
import ExpandRow from "./table/TableExpand.vue";
export default {
  components: { ExpandRow },
  data() {
    return {
      tableHeader: [
        {
          type: "expand",
          width: 50,
          render: (h, params) => {
            return h(ExpandRow, {
              props: {
                row: params.row
              }
            });
          }
        },
        {
          title: "IP",
          slot: "address"
        },
        {
          title: "是否激活",
          slot: "active"
        },

        {
          title: "cleanSession",
          slot: "cleanSession"
        }
      ],

      pageData: {
        currentSelectedTopics: [],
        page: 0,
        size: 10,
        total: 0,
        data: []
      }
    };
  },
  created() {
    /**
      初始化数据
       */
    this.loadData(0, 10);
  },
  methods: {
    /**
      onPageChange
       */
    onPageChange(page) {
      console.log("Page改变:", page);
      this.pageData.page += 1;
      this.loadData(page, this.pageData.size);
    },
    /**
     * http://localhost:2580/dashboard/searchTopicByChannelId?channelId=00a16
     */
    getTopicInfo(channelId) {
      let _this = this;
      console.log("加载Topic数据:", channelId);
      this.axios
        .get(
          "http://" +
            config.host +
            ":" +
            config.port +
            config.context +
            "dashboard/searchTopicByChannelId",
          {
            params: {
              channelId: channelId
            },
            headers: {
              web_console_token: localStorage.getItem("web_console_token")
            }
          }
        )
        .then(response => {
          console.log(response.data);
          if (response.data.state == 1) {
            _this.pageData.currentSelectedTopics = response.data.data;
            //console.log("TOPIC:", _this.pageData.currentSelectedTopics);
          } else {
            this.$Message.error("数据请求失败");
          }
        })
        .catch(response => {
          console.log(response);
        });
    },
    /**
     * 一行数据点击
     */
    onRowClick(data, index) {
      
    },
    /**
     *点击展开行
     */
    onExpand(row, status) {
      this.getTopicInfo(row.key);
    },
    /**
      从服务器请求数据
      http://localhost:2580/dashboard/channels?page=1&size=20

       */
    loadData(page, size) {
      let _this = this;
      console.log("加载数据:", page, size);
      this.axios
        .get(
          "http://" +
            config.host +
            ":" +
            config.port +
            config.context +
            "dashboard/channels",
          {
            params: { page: page, size: size },
            headers: {
              web_console_token: localStorage.getItem("web_console_token")
            }
          }
        )
        .then(response => {
          console.log(response.data);
          if (response.data.state == 1) {
            _this.pageData.data = response.data.data;
            _this.pageData.total = response.data.total;
          } else {
            this.$Message.error("数据请求失败");
          }
        })
        .catch(response => {
          console.log(response);
        });
    }
  }
};
</script>